* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 100vh;
}

/* 
.navbar {

  background-color: aqua !important;
} */
.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px grey;
}

/* icon */

.switch-icon {
  border: 1px solid rgba(0, 0, 0, 0.426);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.unactive-icon {
  color: grey;
}

.active-icon {
  color: black;
}